@use './variables' as v;
html {
    font-size: 62.5%;
    box-sizing: border-box;
}
*, *::before, *::after {
    box-sizing: inherit;
}
body {
    font-family: v.$fuente_principal;
    font-size: 1.6rem;
}
p {
    color: v.$negro;
}
.box {
    width: 95%;
    margin: 0 auto;
    max-width: 120rem;
}
a {
    text-decoration: none;
}
img {
    max-width: 100%;
    display: block;
}
h1, h2, h3 {
    margin: 0 0 v.$gap 0;
}
h1 {
    font-size: 4rem;
}
h2 {
    font-size: 3.6rem;
}
h3 {
    font-size: 3.2rem;
}